@import url('https://fonts.googleapis.com/css2?family=Andada+Pro&family=Lora&family=Montserrat:ital@1&family=Nunito+Sans&family=Source+Code+Pro&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Andada+Pro&family=Lora&family=Montserrat:ital@1&family=Nunito+Sans&family=Source+Code+Pro&family=Zen+Kurenaido&display=swap');
.input_box{
     display: grid;
     place-items: center;
     background-color: #d6f3f3;
     height: 100vh;
}
.input_field >h1{
    font-family: 'Lora', serif;
}
.input_field >p{
    font-family:'Zen Kurenaido', sans-serif ;
}
.googleImage{
    width: 150px;
    height: 80px;
    
    
}
.input_field{
    width: 60%;
    padding: 10px;
    text-align: center;
    background-color: rgb(251, 251, 251);
    box-shadow: 0 5px 6px rgba(0,0,0,0.12),0 2px 3px rgba(0,0,0,0.24);
    border-radius: 10px;
    padding-bottom: 50px;
    
}
.input_field > form> button{
    margin-top: 30px;
    color: white;
}